// Simplemde Demo
// =============================================================

const simpleMDEDemo = {

  init () {

    this.bindUIActions()

  },

  bindUIActions () {

    // event handlers
    this.handleSimpleMDE()
  },

  handleSimpleMDE () {
    new SimpleMDE({
      element: $('#simplemde')[0],
      spellChecker: false,
      autosave: {
        enabled: true,
        unique_id: 'SimpleMDEDemo'
      },
    })
  }
}

simpleMDEDemo.init()
